<template>
  <b-container v-if="respond !== undefined" class="">
    <div v-if="contractor !== undefined" class="header">
      <div class="avatar">
        <span v-if="contractor.avatar === null">{{
          contractor.name.slice(0, 1)
        }}</span>
      </div>

      <router-link :to="contractorLinkUrl"
        ><h1 class="mb-0 ml-3">{{ contractor.name }}</h1></router-link
      >
    </div>
    <div v-else>
      Nope
    </div>

    <div class="separator"></div>

    <div class="send-change d-flex">
      <span class="time mr-2">Отправлено: {{ respond.creationTime }}</span>
      <span class="time" v-if="respond.creationTime !== respond.updateTime">
        Изменено: {{ respond.updateTime }}
      </span>
    </div>

    <div v-if="request !== undefined">
      <router-link :to="requestLinkUrl" class="request">
        Заявка: {{ request.name }}</router-link
      >

      <div class="adress">{{ request.city }}</div>
    </div>

    <div class="properties">
      <div v-if="request !== undefined" class="mb-3">
        <p>
          Заявленые сроки: <span>{{ request.period }}</span>
        </p>
        <p>
          Заявленая сумма: <span>{{ request.budjet }}</span>
        </p>
      </div>
      <div>
        <p>
          Оценочные сроки: <span>{{ respond.period }}</span>
        </p>
        <p>
          Оценочная сумма: <span>{{ respond.cost }}</span>
        </p>
      </div>
    </div>

    <div class="description">
      <p class="head">Описание отклика</p>
      <p class="body pb-0">
        {{ respond.description }}
      </p>
    </div>

    <div class="buttons">
      <button class="btn btn-primary mr-4">Выбрать этого исполнителя</button>
      <button class="btn btn-secondary">Отклонить отклик</button>
    </div>
  </b-container>
</template>

<script>
export default {
  name: "RespondCustomer",
  computed: {
    respond() {
      return this.$store.getters.getResponseById(
        this.$route.params.respondId
      )[0];
    },
    contractor() {
      return this.$store.getters.getContractorById(this.respond.contractorId);
    },
    request() {
      return this.$store.getters.getRequestById(this.respond.requestId);
    },
    requestLinkUrl() {
      return "/allRequests/" + this.request.id;
    },
    contractorLinkUrl() {
      return "/contractor/" + this.contractor.id;
    }
  },
  mounted() {}
};
</script>

